// var _ = require('underscore')

angular.module('well', [
  'ngRoute',
  'well.controllers',
  'well.filters',
  'well.services',
  'well.directives',
  'well.survey',
  'well.tradeoff',
  'well.overlap',
  'well.instruct',
  'well.results'
])
.config(function ($routeProvider, $locationProvider) {
  $routeProvider
    .when('/', {
      templateUrl: 'landing.html'
    })
    .when('/auth', {
      templateUrl: 'auth.html'
    })
    .when('/consent', {
      templateUrl: 'consent.html'
    })
    .when('/terms', {
      templateUrl: 'terms.html'
    })
    .when('/privacy', {
      templateUrl: 'privacy.html'
    })
    // survey
    .when('/:module/survey', {
      templateUrl: 'survey.html',
      controller: 'SurveyCtrl'
    })
    .when('/sur', {
      templateUrl: 'survey.html',
      controller: 'SurveyCtrl'
    })
    .when('/transition', {
      templateUrl: 'transition.html',
      controller: 'TransCtrl'
    })
    // instructions
    .when('/:module/instructions', {
      templateUrl: 'instructions.html',
      controller: 'InstructCtrl'
    })
    // ThreeTwo Module
    .when('/threetwo/abbcca', {
      templateUrl: 'tradeoff.html',
      controller: 'ThreeCtrl'
    })
    .when('/threetwo/dbec', {
      templateUrl: 'tradeoff.html',
      controller: 'TwoCtrl'
    })
    // Overlap Module
    .when('/overlap/transition', {
      templateUrl: 'transition.html',
      controller: 'TransCtrl'
    })
    .when('/overlap/abac', {
      templateUrl: 'tradeoff.html',
      controller: 'AbacCtrl'
    })
    .when('/overlap/abc', {
      templateUrl: 'overlap.html',
      controller: 'AbcCtrl'
    })
    // Fine Overlap Module
    .when('/fineoverlap/tradeoff', {
      templateUrl: 'fineoverlap.html',
      controller: 'FineCtrl'
    })
    // exit
    .when('/:module/exit', {
      templateUrl: 'exit.html',
      controller: 'ExitCtrl'
    })
    // Misc
    .when('/chart', {
      templateUrl: 'chart.html',
      controller: 'ChartCtrl'
    })
    .when('/dummy', {
      templateUrl: 'dummy.html',
      controller: 'DummyCtrl'
    })
    .when('/test', {
      templateUrl: 'test.html',
      controller: 'TestCtrl'
    })
    .otherwise({
      redirectTo: '/'
    })

  // $locationProvider.html5Mode(true)
})
.run(() => {
  if (navigator.userAgent.indexOf('Mozilla') === -1) {
    alert('Bad browser!')
  }
})
